<template>
<nav id="theNav">
	<div>
		<i @click="$router.go(-1)" class="material-icons">arrow_back</i>
		<h2 style="cursor:pointer;" @click="changeFolderName()" id="folderName"></h2>
	</div>
	<div>
		<div style="display:none;" id="favorite_icon_div" @click="favoriteButton()">
			<i id="favorite_icon" class="material-icons">star</i>
		</div>
		<i @click="showDropdownFolder()" id="dropdown_icon" class="material-icons">arrow_drop_down</i>
	</div>
</nav>
<div id="dropdown-content-folder" class="dropdown-content-folder">
	<a @click="deleteCurrentFolder()" class="titleText">Delete folder</a>
</div>
<br>
<div class="contentPadding">

	<Modal :isLoading="isLoading" :isVisible="modalVisible" :closeModal="toggleModal" :options="options" :functions="functions"/>

	<h3 v-if="folders.length==0&&notes.length==0" class="titleText noContent">No content</h3>
	<template v-else>
		<FolderCard v-for="(folder,index) in folders" @contextmenu.prevent="folderMenu(folder.id)" :key="index" :id="folder.id">{{folder.name}}</FolderCard>
		<NoteCard v-for="(note,index) in notes" @contextmenu.prevent="noteMenu(note.id)" :key="index" :id="note.id">{{note.title}}</NoteCard>
	</template>

</div>
<br>
<div class="folderAddButtons">
	<div @click="addNewFolder()" class="circularButton">
		<i style="font-size:1.8em" class="material-icons">create_new_folder</i>
	</div>
	<div @click="addNewNote()" class="circularButton">
		<i style="font-size:1.8em" class="material-icons">note_add</i>
	</div>
</div>
<br>
</template>

<script>
import {reactive} from 'vue';
import { ref } from "vue";
import { useRoute } from 'vue-router';
import Localbase from 'localbase';
import FolderCard from '../components/FolderCard.vue';
import NoteCard from '../components/NoteCard.vue';
import Modal from '../components/Modal.vue';

export default {
  name: 'FolderView',
  components: {
		FolderCard,
		NoteCard,
		Modal,
  },
  setup(props,context){

		let alwaysTrue = true;

		let db = new Localbase('monkeynote');
		const route = useRoute();
		let folder;

		const folders=reactive([]);
		const notes=reactive([]);

		const modalVisible = ref(false);
		const isLoading = ref(false);
		const options = reactive([]);
		const functions = reactive([]);

		function toggleModal() {
			modalVisible.value=!modalVisible.value;
		}

		async function fetchFolderContent(){
			let foldersRaw = [];
			let notesRaw = [];
			//Get the folder
			folder = await db.collection("folders").doc(route.params.folderID).get();
			console.log(folder);
			//Get folder IDs
			let foldersIDs;
			if(folder.foldersID.trim()==""){
				foldersIDs = [];
			} else {
				foldersIDs = folder.foldersID.split(" ");
			}
			//Get folders
			for (let i = foldersIDs.length - 1; i >= 0; i--) {
				let x = await db.collection("folders").doc(foldersIDs[i]).get();
				foldersRaw.push({id:foldersIDs[i],name:x.name});
			}
			//Get notes IDs
			let notesIDs;
			if(folder.notesID.trim()==""){
				notesIDs = [];
			} else {
				notesIDs = folder.notesID.split(" ");
			}
			//Get notes
			for (let i = notesIDs.length - 1; i >= 0; i--) {
				let x = await db.collection("notes").doc(notesIDs[i]).get();
				notesRaw.push({id:notesIDs[i],title:x.title});
			}

			//Set the title
			document.getElementById("folderName").innerHTML=folder.name;

			//Whether it's favorite or not
			if(!folder.favorite){
				document.getElementById("favorite_icon").innerHTML="star_border";
			}
			document.getElementById("favorite_icon_div").style.display = "block";

			//Update the UI folder content
			while (folders.length) {
				folders.pop();
			}
			while (notes.length) {
				notes.pop();
			}
			foldersRaw.forEach(function(element,index){
				folders.push(element);
			});
			notesRaw.forEach(function(element,index){
				notes.push(element);
			});
		}
		fetchFolderContent();

		function changeFolderName(){
			let newFolderName = prompt("New folder name:",folder.name);
			if(newFolderName!=null&&newFolderName.trim()!=""){
				db.collection('folders').doc(route.params.folderID).update({
					name: newFolderName,
				}).then(response => {
					folder.name = newFolderName;
					document.getElementById("folderName").innerHTML=newFolderName;
				}).catch(error => {
					alert('There was an error');
				});
			}
		}
		
		async function addNewFolder(){

			let name = prompt("New Folder:");
			if (name!=null&&name.trim()!="") {
				
				let db = new Localbase('monkeynote');
				//Create the new folder
				let response = await db.collection('folders').add({
					parentID: route.params.folderID,
					name: name,
					notesID:"",
					foldersID:"",
					favorite:false,
				});
				//Add the new folder key into its parent folders ID list
				let key = response.substring(response.indexOf('"key":"')+7,response.indexOf('","data"'));
				if(folder.foldersID.trim()==""){
					folder.foldersID=key;
					await db.collection('folders').doc(route.params.folderID).update({
						foldersID: key,
					});
				} else {
					let a = folder.foldersID.split(" ");
					a.push(key);
					folder.foldersID = a.join(" ");
					await db.collection('folders').doc(route.params.folderID).update({
						foldersID: a.join(" "),
					});
				}

				//Update the changes in the UI
				folders.push({id:key,name:name});
			}
		}
		
		async function addNewNote(){
			let title = prompt("New Note:");
			if (title!=null&&title.trim()!="") {
				let db = new Localbase('monkeynote');
				let milliseconds = (new Date()).getTime();//milliseconds since epoch
				//Create the new note
				let response = await db.collection('notes').add({
					folderID: route.params.folderID,
					title: title,
					body: "",
					favorite:false,
					creationDate: milliseconds,
					lastModified: milliseconds,
				});
				//Add the new note key into its parent folders ID list
				let key = response.substring(response.indexOf('"key":"')+7,response.indexOf('","data"'));
				if(folder.notesID.trim()==""){
					folder.notesID=key;
					await db.collection('folders').doc(route.params.folderID).update({
						notesID: key,
					});
				} else {
					let a = folder.notesID.split(" ");
					a.push(key);
					folder.notesID = a.join(" ");
					await db.collection('folders').doc(route.params.folderID).update({
						notesID: a.join(" "),
					});
				}

				//Update the changes in the UI
				notes.push({id:key,title:title});
			}
		}

		function folderMenu(id){
			//Clear the previous content
			while (options.length) {
				options.pop();
			}
			//Add the options
			options.push("Delete folder");
			//Clear the previous content
			while (functions.length) {
				functions.pop();
			}
			//Add the options
			functions.push(async function(){
				isLoading.value=true;
				if(confirm("Delete the folder?")){
					await deleteFolder(id);
				}
				await fetchFolderContent();
				isLoading.value=false;
				modalVisible.value=false;
			});
			//Show the modal
			toggleModal();
		}

		function noteMenu(id){
			//Clear the previous content
			while (options.length) {
				options.pop();
			}
			//Add the options
			options.push("Delete note");
			//Clear the previous content
			while (functions.length) {
				functions.pop();
			}
			//Add the options
			functions.push(async function(){
				isLoading.value=true;
				if(confirm("Delete the note?")){
					await deleteNote(id);
				}
				await fetchFolderContent();
				isLoading.value=false;
				modalVisible.value=false;
			});
			//Show the modal
			toggleModal();
		}

		async function deleteFolder(folderID){
			try {
				//Get the folder to be deleted
				let folderToBeDeleted = await db.collection("folders").doc(folderID).get();
				//Remove from favorites
				if(folderToBeDeleted.favorite){
					let x = localStorage.getItem("favoriteFolders").split(" ");
					x.splice(x.indexOf(folderID),1);
					while(alwaysTrue){//Delete the empty items
						if(x.indexOf("")!=-1){
							x.splice(x.indexOf(""),1);
						} else {
							break;
						}
					}
					localStorage.setItem("favoriteFolders",x.join(" "));
				}
				//Remove it from its parent folder list of folders
				if(folderToBeDeleted.parentID==null){
					//It's in the main folder
					let y = localStorage.getItem("mainFolderFolders").split(" ");
					y.splice(y.indexOf(folderID),1);
					while(alwaysTrue){//Delete the empty items
						if(y.indexOf("")!=-1){
							y.splice(y.indexOf(""),1);
						} else {
							break;
						}
					}
					localStorage.setItem("mainFolderFolders",y.join(" "));
				} else {
					//It's not in the main folder
					let parentFolder = await db.collection("folders").doc(folderToBeDeleted.parentID).get();
					let z = parentFolder.foldersID.split(" ");
					z.splice(z.indexOf(folderID),1);
					await db.collection("folders").doc(folderToBeDeleted.parentID).update({
						foldersID: z.join(" "),
					});
				}
				//Delete all notes inside the folder to be deleted
				if(folderToBeDeleted.notesID!=""&&folderToBeDeleted.notesID!=" "){
					let w = folderToBeDeleted.notesID.split(" ");
					for (let i = w.length - 1; i >= 0; i--) {
						await deleteNote(w[i]);
					}
				}
				//Delete all folders inside
				if(folderToBeDeleted.foldersID!=""&&folderToBeDeleted.foldersID!=" "){
					let k = folderToBeDeleted.foldersID.split(" ");
					for (let i = k.length - 1; i >= 0; i--) {
						await deleteFolder(k[i]);
					}
				}
				//Delete the folder itself from localbase
				await db.collection("folders").doc(folderID).delete();
			} catch(e) {
				alert("There was an error");
			}
		}

		async function deleteNote(noteID){
			//Get the note
			let noteToBeDeleted = await db.collection("notes").doc(noteID).get();
			//Remove it from favorites if favorite
			if(noteToBeDeleted.favorite){
				let x = localStorage.getItem("favoriteNotes").split(" ");
				x.splice(x.indexOf(noteID),1);
				while(alwaysTrue){//Delete the empty items
					if(x.indexOf("")!=-1){
						x.splice(x.indexOf(""),1);
					} else {
						break;
					}
				}
				localStorage.setItem("favoriteNotes",x.join(" "));
			}
			//Remove it from its parent folder list of notes
			let parentFolderID = noteToBeDeleted.folderID;
			let parentFolderNoteIDs = (await db.collection("folders").doc(parentFolderID).get()).notesID.split(" ");
			parentFolderNoteIDs.splice(parentFolderNoteIDs.indexOf(noteID),1);
			await db.collection('folders').doc(parentFolderID).update({
				notesID: parentFolderNoteIDs.join(" "),
			});
			//Update the info in the note
			await db.collection('notes').doc(noteID).update({folderID:null,favorite:false});
			//Add it to the recycle bin
			if(localStorage.getItem("deletedNotes")==null||localStorage.getItem("deletedNotes")==""){
				localStorage.setItem("deletedNotes",noteID);
			} else {
				let x = localStorage.getItem("deletedNotes").split(" ");
				x.push(noteID);
				while(alwaysTrue){//Delete the empty items
					if(x.indexOf("")!=-1){
						x.splice(x.indexOf(""),1);
					} else {
						break;
					}
				}
				localStorage.setItem("deletedNotes",x.join(" "));
			}
		}
		
		//Change if the current element is favorite or not
		function favoriteButton(){
			let favorite_icon = document.getElementById("favorite_icon");
			db.collection('folders').doc(route.params.folderID).update({
				favorite: favorite_icon.innerHTML=="star_border",
			}).then(response => {
				//Change the icon and save it into the favorite folders
				if(favorite_icon.innerHTML=="star"){
					favorite_icon.innerHTML="star_border";
					let mirkoDebil = localStorage.getItem("favoriteFolders").split(" ");
					mirkoDebil.splice(mirkoDebil.indexOf(route.params.folderID),1);
					while(alwaysTrue){//Delete the empty items
						if(mirkoDebil.indexOf("")!=-1){
							mirkoDebil.splice(mirkoDebil.indexOf(""),1);
						} else {
							break;
						}
					}
					mirkoDebil=mirkoDebil.filter((item,index)=>mirkoDebil.indexOf(item)===index);//Remove duplicated items
					localStorage.setItem("favoriteFolders",mirkoDebil.join(" "));
				} else {
					favorite_icon.innerHTML="star";
					//Save it into the favorite folders
					if(localStorage.getItem("favoriteFolders")==null||localStorage.getItem("favoriteFolders")==""){
						localStorage.setItem("favoriteFolders",route.params.folderID);
					} else {
						let renzitoInutil = localStorage.getItem("favoriteFolders").split(" ");
						if(renzitoInutil.indexOf(route.params.noteID)==-1){
							renzitoInutil.push(route.params.folderID);
						}
						while(alwaysTrue){//Delete the empty items
							if(renzitoInutil.indexOf("")!=-1){
								renzitoInutil.splice(renzitoInutil.indexOf(""),1);
							} else {
								break;
							}
						}
						renzitoInutil=renzitoInutil.filter((item,index)=>renzitoInutil.indexOf(item)===index);//Remove duplicated items
						localStorage.setItem("favoriteFolders",renzitoInutil.join(" "));
					}
				}
			}).catch(error => {
				alert('There was an error');
			});
		}
		
		function showDropdownFolder(){
			let dropdownIcon = document.getElementById("dropdown_icon");
			if(document.getElementById("dropdown-content-folder").style.display=="block"){
				dropdownIcon.innerHTML="arrow_drop_down";
				document.getElementById("dropdown-content-folder").style.display="none";
			} else {
				dropdownIcon.innerHTML="arrow_drop_up";
				document.getElementById("dropdown-content-folder").style.display="block";
			}
		}
		
		async function deleteCurrentFolder(){
			if(confirm("Are you sure you want to delete the folder?")){
				isLoading.value=true;
				modalVisible.value=true;
				try {
					await deleteFolder(route.params.folderID);
				} catch(e) {
					alert('There was an error');
				}
				modalVisible.value=false;
				isLoading.value=false;
				history.back();
			}
		}
		
		return {
			toggleModal,
			modalVisible,
			options,
			functions,
			folderMenu,
			noteMenu,
			addNewFolder,
			addNewNote,
			favoriteButton,
			showDropdownFolder,
			changeFolderName,
			folders,
			notes,
			deleteCurrentFolder,
			fetchFolderContent,
			isLoading,
		}
  }
}
</script>

