<template>

	<Modal :isLoading="isLoading" :isVisible="modalVisible" :closeModal="toggleModal" :options="options" :functions="functions"/>

	<div class="monkeyBar">
		<i @click="$router.go(-1)" class="material-icons">arrow_back</i>
		<h3 class="titleText">Recycle bin</h3>
	</div>
	<div v-if="!(loading.value)" id="recycleBinContent">
		
		<h3 v-if="deletedNotes.length==0" class="titleText noContent">No content</h3>
		<template v-else>
			<NoteCard v-for="(note,index) in deletedNotes" :key="index" @contextmenu.prevent="deletedNoteMenu(note.id)" :id="note.id">{{note.title}}</NoteCard>
		</template>

	</div>
	<div style="display:flex;justify-content:flex-end;">
		<div class="tooltip">
			<div @click="clearRecycleBin()" class="circularButton">
				<i style="font-size:1.9em" class="material-icons" aria-hidden="true">delete_forever</i>
			</div>
			<span class="tooltiptext">delete all</span>
		</div>
	</div>
</template>

<script>
import {reactive} from 'vue';
import { ref } from "vue";
import Localbase from 'localbase';
import NoteCard from '../components/NoteCard.vue';
import Modal from '../components/Modal.vue';
export default {
  name: 'RecycleBin',
  components: {
		NoteCard,
		Modal,
  },
  setup(props,context){

		let alwaysTrue = true;

		let db = new Localbase('monkeynote');
		
		let deletedNotes = reactive([]);
		let loading = ref(true);

		let isLoading = ref(false);
		let modalVisible = ref(false);
		let options = reactive([]);
		let functions = reactive([]);

		function toggleModal() {
			modalVisible.value=!modalVisible.value;
		}

		async function getRecycleBinContent(){
			//Clear the previous content
			while (deletedNotes.length) {
				deletedNotes.pop();
			}
			//Get the list of deleted notes IDs
			if(localStorage.getItem("deletedNotes")==null||localStorage.getItem("deletedNotes")==""){
				//The recycle bin is empty
				loading.value = false;
			} else {
				let a = localStorage.getItem("deletedNotes").split(" ");
				try {
					for (let i = a.length - 1; i >= 0; i--) {
						let x = await db.collection("notes").doc(a[i]).get();
						deletedNotes.push({id:a[i],title:x.title});
					}
					loading.value = false;
				} catch(e) {
					console.log(e);
					alert('There was an error');
				}
			}
		}
		getRecycleBinContent();

		async function clearRecycleBin(){
			if(confirm("Are you sure you want to delete everything?")){
				try {
					//Delete each note
					for (let i = deletedNotes.length - 1; i >= 0; i--) {
						try {
							await db.collection("notes").doc(deletedNotes[i].id).delete();
						} catch(e) {
							// Do nothing
						}
					}
					//Clear the deletedNotes
					while (deletedNotes.length) {
						deletedNotes.pop();
					}
					//Clear the deletedNotes in the Local Storage
					localStorage.setItem("deletedNotes","");
				} catch(e) {
					console.log(e);
					alert('There was an error');
				}
			}
		}

		function deletedNoteMenu(id){
			//Clear the previous content
			while (options.length) {
				options.pop();
			}
			//Add the options
			options.push("Restore note");
			options.push("Delete note");
			//Clear the previous content
			while (functions.length) {
				functions.pop();
			}
			//Add the options
			functions.push(async function(){
				isLoading.value=true;
				if(confirm("Restore the note?")){
					await restoreNote(id);
				}
				await getRecycleBinContent();
				isLoading.value=false;
				modalVisible.value=false;
			});
			functions.push(async function(){
				isLoading.value=true;
				if(confirm("Delete the note forever?")){
					await deleteNoteForever(id);
				}
				await getRecycleBinContent();
				isLoading.value=false;
				modalVisible.value=false;
			});
			//Show the modal
			toggleModal();
		}

		// TODO
		async function deleteNoteForever(id){
			//Remove it from the list of deleted notes
			let x = localStorage.getItem("deletedNotes").split(" ");
			x.splice(x.indexOf(id),1);
			while(alwaysTrue){//Delete the empty items
				if(x.indexOf("")!=-1){
					x.splice(x.indexOf(""),1);
				} else {
					break;
				}
			}
			localStorage.setItem("deletedNotes",x.join(" "));
			//Delete the note itself
			try {
				await db.collection("notes").doc(id).delete();
			} catch(e) {
				alert('There was an error');
			}
		}

		async function restoreNote(id){
			try {
				//Remove the note ID from deletedNotes in Local Storage
				let x = localStorage.getItem("deletedNotes").split(" ");
				x.splice(x.indexOf(id),1);
				while(alwaysTrue){//Delete the empty items
					if(x.indexOf("")!=-1){
						x.splice(x.indexOf(""),1);
					} else {
						break;
					}
				}
				localStorage.setItem("deletedNotes",x.join(" "));
				//Add the note ID into mainFolderNotes in Local Storage
				let y = localStorage.getItem("mainFolderNotes");
				if(y==null||y=="") {
					localStorage.setItem("mainFolderNotes",id);
				} else {
					let z = y.split(" ");
					z.push(id);
					while(alwaysTrue){//Delete the empty items
						if(z.indexOf("")!=-1){
							z.splice(z.indexOf(""),1);
						} else {
							break;
						}
					}
					localStorage.setItem("mainFolderNotes",z.join(" "));
				}
			} catch(e) {
				console.log(e);
				alert('There was an error');
			}
		}
		
		return {
			deletedNotes,
			clearRecycleBin,
			loading,
			isLoading,
			modalVisible,
			options,
			functions,
			toggleModal,
			deletedNoteMenu,
			deleteNoteForever,
		};
  }
}
</script>

<style scoped>
	.tooltip {
		border: none;
		position: relative;
		display: inline-block;
	}
	.tooltip .tooltiptext {
		visibility: hidden;
		width: 120px;
		background-color: black;
		color: #fff;
		text-align: center;
		border-radius: 6px;
		padding: 5px 0;
		position: absolute;
		z-index: 1;
		top: 50px;
		right: 100%;
	}
	.tooltip .tooltiptext::after {
		content: "";
		position: absolute;
		top: 50%;
		left: 100%;
		margin-top: -5px;
		border-width: 5px;
		border-style: solid;
		border-color: transparent transparent transparent black;
	}
	.tooltip:hover .tooltiptext {
		visibility: visible;
	}
</style>