		background-color: rgb(121,85,72);
		color: white;
		font-size: 1em;
		box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
		z-index: 1;
		/*Flex box*/
		display: flex;
		flex-wrap: nowrap;
		align-items: center;
		justify-content: space-between;
	}
	nav div:nth-child(1) i {
		cursor: pointer;
	}
	nav div:nth-child(1) {
		display: flex;
		align-items: center;
	}
	nav div:nth-child(2) {
		display: flex;
		align-items: center;
	}
	nav div:nth-child(2) i {
		margin-right: 20px;
		margin-left: 20px;
		cursor: pointer;
	}
	nav h1 {
		margin: 0;
		padding-left: 15px;
		font-size: 33px;
		display: inline;
	}
	nav i {
		padding: 15px;
	}
	nav i:hover {
		background-color: rgb(88,62,52);
	}
	@media screen and (max-width: 770px) {/*For tablets*/
		nav h1 {
			padding: 0;
			font-size: 23px;
		}
		nav div:nth-child(2) i {
			margin-right: 0px;
			margin-left: 0px;
		}
	}

	.dropdown-content-note {
		display: none;
		background-color: rgb(121,85,72);
		width: 80%;
		margin: 0px 0px 0px auto;
		box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
		z-index: 1;
		position:fixed;
		right: 0px;
